<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="current-month-title">{{monthText}} 月消費清單</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="current-month-cost-outer-frame">
          <div
            class="current-month-cost-outer"
            :class="{'current-month-cost-outer-active':currentCost}"
          >
            <div class="current-month-cost">
              <table class="table">
                <thead>
                  <tr>
                    <th scope="col">名稱</th>
                    <th scope="col">項目</th>
                    <th scope="col">類別</th>
                    <th scope="col">金額</th>
                    <th scope="col">日期</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(costs,index) in showCostGroup" :key="index">
                    <td>{{costs.costName}}</td>
                    <td>{{costs.costType}}</td>
                    <td>{{costs.costWay}}</td>
                    <td :class="{'alert':costs.costCash>=1000}">{{costs.costCash}} 元</td>
                    <td>{{costs.costFullDate.slice(7,14)}}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="5">花費總額 {{allCostCount}} 元</td>
                  </tr>
                </tfoot>
              </table>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .current-month-title {
    display: block;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    padding: 20px 0 20px 0;
    color: white;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .current-month-cost-outer-frame {
    overflow: hidden;
    .current-month-cost-outer {
      margin-top: -700px;
      transition: 1s ease;
      .current-month-cost {
        border-radius: 10px;
        box-shadow: inset 0 0 4px 2px rgba(255, 255, 255, 0.7);
        overflow: hidden;
        .table {
          color: white;
          background-color: rgba(0, 0, 0, 0.7);
          margin-bottom: 0;
          table-layout: fixed;
          text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
          thead {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              th {
                text-align: center;
                border-top: none;
              }
            }
          }
          tbody {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              text-align: center;
              transition: 0.7s ease;
              td {
                text-align: center;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
          tfoot {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              td {
                text-align: right;
              }
            }
          }
          .alert {
            color: red;
            font-weight: bold;
            border-bottom: 1px solid white;
          }
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .current-month-cost-outer-active {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      showCostGroup: [],
      showCostGroupTemp: [],
      allCostNum: [],
      allCostCount: "",
      monthText: "",
      yearItem: [
        {
          yearNum: 0,
          year: 2020,
        },
        {
          yearNum: 1,
          year: 2021,
        },
        {
          yearNum: 2,
          year: 2022,
        },
        {
          yearNum: 3,
          year: 2023,
        },
        {
          yearNum: 4,
          year: 2024,
        },
        {
          yearNum: 5,
          year: 2025,
        },
      ],
      yearNumTemp: null,
      pageAnimate: false,
      currentCost: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.currentCost = true), 1100);
  },
  methods: {
    goBack() {
      this.currentCost = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    timeObject() {
      const time = new Date();
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      this.monthText = month;
      this.getCost(year, month);
    },
    getCost(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/cost/0/year/${year}/month.json`
        )
        .then((res) => {
          this.showCostGroupTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.showCostGroupTemp = this.showCostGroupTemp.sort(
            (max, min) => max.costDate - min.costDate
          );
          this.showCostGroupTemp.forEach((key) =>
            key.costMonth == month ? this.showCostGroup.push(key) : null
          );
          this.showCostGroup.forEach((key) =>
            this.allCostNum.push(Number(key.costCash))
          );
          this.allCostCount = this.allCostNum.reduce(
            (front, end) => front + end
          );
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.timeObject();
  },
};
</script>